.input {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-top: auto;
  position: relative;
}

.input_wrap {
  margin-right: 10px;
  flex-grow: 1;
}

.empty_field,
.exist_field {
  position: relative;
}

.exist_field:before {
  content: 'Такая задача уже есть в вашем списке. Введите другое название';
  color: #fff;
  background: #356eff;
  border-radius: 6px;
  font: normal normal 14px/16px 'Roboto';
  height: auto;
  max-width: 240px;
  padding: 8px 11px;
  position: absolute;
  bottom: 50px;
  left: 20px;
}

.empty_field:before {
  content: 'Поле должно быть заполнено!';
  color: #fff;
  background: #356eff;
  border-radius: 6px;
  font: normal normal 14px/16px' Roboto';
  height: auto;
  width: auto;
  padding: 8px 11px;
  position: absolute;
  bottom: 50px;
  left: 20px;
}

.exist_field:after,
.empty_field:after {
  content: '';
  position: absolute;
  left: 10px;
  border: 10px solid transparent;
  border-top-color: #356eff;
  bottom: 31px;
  left: 40px;
}

.input_field {
  border: 1px solid #ccc;
  box-sizing: border-box;
  border-radius: 41px;
  height: 36px;
  width: 100%;
  outline: none;
  padding: 0 12px;
}


.button {
  background: url('./img/plus.svg');
  height: 36px;
  width: 36px;
}

.button:hover {
  cursor: pointer;
  opacity: 0.9;
}

@media (min-width: 321px) and (max-width: 409px) {

  .exist_field:before,
  .empty_field:before {
    font: normal normal 12px/16px 'Roboto';
  }
}

@media (max-width: 320px) {

  .input_field {
    font-size: 10px;
  }
}
